import React from "react";
import PropTypes from "prop-types";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  /* 
  setState를 할때마다 react는 다시 새로운 state로 render함.
  react는 DOM객체를 기억하고있어서 수정된 부분만 바뀜
  미리 선언해도되고 안해도됨
  */
  state = {
    isLoading: true,
    movies: [],
  };
  // async await - 비동기(axios가 다 로딩될까지 기다려야한다는걸 알려줌)
  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sortby=rating"
    );
    // this.movies에 axios movies를 넣어줌(this.setState({movies : movies}) 와 같음)
    this.setState({ movies, isLoading: false });
  };
  componentDidMount() {
    this.getMovies();
  }
  // 리액트는 자동으로 class component의 render 메소드를 실행함
  render() {
    const { isLoading, movies } = this.state;
    // map - rendering
    // map은 array 반환
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
